* {
    padding: 0px;
    margin: 0px;
    list-style: none;
}

nav {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    height: 30px;
    margin: 10px 0px;
    width: 100%;
}

.logo {
    font-size: 30px;
    margin-left: 8%;
    font-weight: bold;
    justify-content: center;
    align-items: center;
}
.menu {
    display: flex;
    justify-content: center;
    align-items: center;

}

.menu ul {
    display: flex;
    flex-direction: row;
    gap: 30px;
    font-size: 20px;


}

.log {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 70px;
}

.log ul {
    display: flex;
    flex-direction: row;
    gap: 30px;
    font-size: 20px;
}

.btn {
    background-color: black;
    color: white;
    border-radius: 20px;
    padding: 5px 15px
}

.container {
    height: 100%;
    width: 100%;
    display: flex;
    margin-top: 30px;
}

.left {
    flex:1;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    font-size: 100px;
    margin-top: 10%;
    
}

.right {
    flex: 2;
    display: flex;
    flex-direction: row;
    gap: 30px;
    margin-right: 70px;
    animation : width 1s ease forwards

}

.image1 {
    background-image: url('./images/6183bce7320d030e82c15028_hero_person-1-p-500.webp');

}

.image2 {
    background-image: url('./images/6183bce3e584104be2e330e0_hero_person-2-p-500.webp');
}

.image3 {
    background-image: url('./images/6183bb01e5841050e8e31f3b_hero_person-3-p-500.webp');
}

.right > div{
    display: flex;
    align-items: flex-end;
    width: 33%;
    height: 60%;
    background-size: auto 100%;
    background-position: center top;
    border-radius: 20px;
    padding: 10px;
    cursor: pointer;
    transition: width 1s ease;
}

.right > div:hover {
    width: 100%;


}




