.vert {
    position: relative;
    bottom: 100px;
    left: -75px;
    transform: rotate(-90deg);
    color: white;
    font-size: 30px;
    font-weight: bold;
}

.horizon {
    position: relative;
    bottom: 0px;
    left: 0px;
    color: red;
    font-size: 30px;
    font-weight: bold;

}